import { takeLatest, call, put, all } from 'redux-saga/effects';
import { toast } from 'react-toastify';

import history from '~/services/history';
import api from '~/services/api';

import { signInSuccess, signFailure} from './actions';

export function* SignIn({ payload }) {

    try {
      const { nome} = payload;

      const response =  yield call(api.post, 'teste', {
        nome: `${nome}`,
       
      });
  
  
      yield put(signInSuccess());
  
      history.push('/dashboard')
    } catch (err) {
      toast.error('Falha na autenticação, verifique seus dados');
      yield put(signFailure());
    }



    


}

export function* SignUp({ payload }) {

  try {
    const {name, login, password } = payload;

    const response =  yield call(api.post, 'auth/local/register', {
      username: `${name}`,
      email: `${login}`,
      password: `${password}`,
      
    });

    const { jwt, user } = response.data;
    const token = jwt;
    console.log(jwt, user);

    if (!user.provider) {
      console.tron.error('Usuário é uma instituição');
      return;
    }
    api.defaults.headers.Authorization = `Bearer ${token}`;
    yield put(signInSuccess(token, user));
    toast.info('Cadastro concluído com sucesso!');
    history.push('/dashboard')
  } catch (err) {
    toast.error('Falha no cadastro, verifique seus dados');
    yield put(signFailure());
  }

}

export function setToken ({ payload }) {
  if (!payload) return;

  const { token } = payload.auth;

  if (token) {
    api.defaults.headers.Authorization = `Bearer ${token}`;
  }
 
}
export default all([
  takeLatest('persist/REHYDRATE', setToken),
  takeLatest('@auth/SIGN_IN_REQUEST', SignIn),
  takeLatest('@auth/SIGN_UP_REQUEST', SignUp)
]);