export function signRequest(login, password) { 
  return {
    type: '@auth/SIGN_IN_REQUEST',
    payload: { login, password },

  };
}

export function signInSuccess(token, user) {
  return {
    type: '@auth/SIGN_IN_SUCCESS', 
    payload: { token, user },
  }
}

export function signUpRequest(nome, telefone, comonosconheceu, sim, nao, facebook, linkedin, instagram) {
  return {
    type: '@auth/SIGN_UP_REQUEST', 
    payload: { nome, telefone, comonosconheceu, sim, nao, facebook, linkedin, instagram },
  }
}

export function signFailure() {
  return {
    type: '@auth/SIGN_FAILURE', 
  }
}
