import React from 'react';
import { Form } from '@unform/web';
import Input from '~/components/Form/index';
import { toast } from 'react-toastify';
import axios from 'axios';



export default function SignIn() {

  
  function handleSubmit(data) {

    let  { nome,telefone } = data;

    var facebook = document.getElementById('facebook').checked;
    var linkedin = document.getElementById('linkedin').checked;
    var instagram = document.getElementById('instagram').checked;
    var comonosconheceu = document.getElementById('como-nos-conheceu').value;
    let nomeSobrenome = nome.split(' ');
    telefone = telefone.replace(" - ", "");

    var questionQuaisRedesSociais = [];

    if (document.getElementById('sim').checked === true ) {
      var questionRedesSociais = 'Sim';
    
      if (facebook === true) {
        questionQuaisRedesSociais.push('Facebook ');
      }
      if (linkedin === true) {
        questionQuaisRedesSociais.push('Linkedin ');
      }
      if (instagram === true) {
        questionQuaisRedesSociais.push('Instagram ');
      }

    }  else {
      questionRedesSociais = 'Não';
     
    }
    var apiLink = "http://localhost:8080/";
   
    if(nomeSobrenome.length <= 1 ) {
      toast.error('Nome incompleto, preencha seu nome e sobrenome');
      apiLink = "null";
    } else if (telefone.length < 10 && telefone.length > 11) {
      toast.error('Telefone incompleto, preencha seu telefone corretamente');
      apiLink = "null";
      telefone.value = '';
    }  else if(isNaN(telefone) == true) {
     apiLink = "null";
     document.getElementById('telefone').value = '';
      toast.error('Telefone incompleto, preencha seu telefone');
    }  else if ((document.getElementById("nao").checked) === false && (document.getElementById("sim").checked) === false) {
      toast.error('Informe se possui ou não redes sociais');
      apiLink = "null";

    } 
    else if ((document.getElementById("sim").checked) === true) {
      if (facebook === false && linkedin === false && instagram === false) {
        toast.error('Nenhuma Rede Social foi selecionada!');
        apiLink = "null";
      }
    }
      var response = "null";
      // Request API.
      // Add your own code here to customize or restrict how the public can register new users.
      axios
      .post(`${apiLink}`, {
        nome: `${nome}`,
        telefone: `${telefone}`,
        comonosconheceu: `${comonosconheceu}`,
        possuiredesociais: `${questionRedesSociais}`,
        quais: `${questionQuaisRedesSociais}`,
      })
      .then(response => {
        // Handle success.
        document.getElementById('envio').setAttribute("disabled","disabled");
        document.getElementById('envio').innerText = "Enviado";
        document.getElementById('envio').style.background = "#3fd600"
        toast.info('Tudo feito!');
        toast.success(`${nome}, Sua resposta foi salva com Sucesso, agora é com a gente :)`);
      
      })
      .catch(error => {
        // Handle error.
        console.log('An error occurred:', error);
      });
  

  
 }



   


  function mascaraDeTelefone(){
    let fone = document.getElementById('telefone');
    const textoAtual = fone.value;
    let textoAjustado;


    if(textoAtual.length < 10 && textoAtual.length > 11 ) {
      toast.error('Numero de telefone incorreto, por favor digite novamente!');
      fone.value = '';
    } else {
      const isCelular = textoAtual.length === 11;
      
      if(isCelular) {
          const parte1 = textoAtual.slice(0,2);
          const parte2 = textoAtual.slice(2,5);
          const parte3 = textoAtual.slice(5,11);
          textoAjustado = `${parte1} - ${parte2}${parte3}`        
      } else {
          const parte1 = textoAtual.slice(0,2);
          const parte2 = textoAtual.slice(2,4);
          const parte3 = textoAtual.slice(4,10);
          textoAjustado = `${parte1} - ${parte2}${parte3}`   
      }
      fone.value = textoAjustado;
    }
   
}

function showSocial() {
  document.getElementById('redes-sociais').classList.add('show');
  document.getElementById("nao").checked = false;

}
function notShowSocial() {
  document.getElementById('redes-sociais').classList.remove('show');
  document.getElementById("sim").checked = false;
}

   return (
    <>
        
        <Form onSubmit={handleSubmit}>
        <h1>Desafio EloGroup</h1><br></br>
          <Input type="text" name="nome" required placeholder="Seu nome"/>
          <Input type="text"  required name="telefone" id="telefone" onBlur={mascaraDeTelefone}  minLength="10" maxLength="11" placeholder="Ex: 99 – 99999999"/>
          <select placeholder="Como nos conheceu?" name="como-nos-conheceu" id="como-nos-conheceu">
              <option  disabled hidden defaultValue="null">Como nos conheceu?</option>
              <option value="Tv">TV</option>
              <option value="Internet">Internet</option>
              <option value="Outros">Outros</option>
          </select>
          <div className="question-four">
          <p>Possui rede social:</p>
          <Input type="radio"  id="sim" onClick={showSocial} name="sim" defaultValue="false"/>
          <label htmlFor="sim">Sim</label>

          <Input type="radio" id="nao" onClick={notShowSocial}  name="nao" defaultValue="false"/>
          <label htmlFor="nao" value="false">Não</label>
          
        <div id="redes-sociais">
          <p>Quais:</p>
          <Input type="checkbox" id="facebook" name="facebook"/>Facebook
          <Input type="checkbox" id="linkedin" name="linkedin"/>Linkedin
          <Input type="checkbox" id="instagram" name="instagram"/>Instagram
        </div>
         

          </div>


          <div className="buttons">
            <button type="submit" id="envio">Enviar</button>
          </div>
        </Form>

        
    </>
  ); 

  

}
