import React from 'react';
import ProtTypes from 'prop-types';
import { Wrapper, Content } from './styles';



export default function AuthLayout({children}) {
  return (
    <Wrapper>
      <Content>
        {children}
      </Content>
    </Wrapper>
  );
}

AuthLayout.protoTypes = {
  children: ProtTypes.element.isRequired,
}